.dialogs {
    display: grid;
    grid-template-columns: 5fr 7fr;
}

.dialogsItems {
    padding: 10px;
}

.dialog a {
    color: white;
    text-decoration: none;
}

.dialog a.active {
    color: darkkhaki;
}

.messages {
    padding: 10px;
    display: grid;
    grid-template-columns: 2fr 2fr;
    grid-template-rows: 1fr 1fr 1fr 1fr 1fr 1fr 1fr;
}

.messages .message {
    display: flex;
    justify-content: flex-start;
    align-items: center;
}

.messageItem {
    padding: 10px;
    color: white;
}

.avatarMessage {
    width: 35px;
    height: 35px;
    border-radius: 50%;
    background-color: rgb(40, 134, 53);
}

.friendAvatar {
    width: 50px;
}

.dialogWrapper {
    display: flex;
    justify-content: flex-start;
    align-items: center;
}

.textWrapper {
    display: flex;
    flex-flow: column;
    justify-content: center;
    align-items: flex-end;
    width: 150px;

}

.messageArea {
    height: 45px;
}

.sendMessage {
    width: 50px;
    height: 40px;
    background-color: rgb(40, 134, 53);
    
}